import React, { useState } from "react";
import { Link } from "react-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import "./Navbar.css";

function Navbar() {
  const [showMenu, setShowMenu] = useState(false);

  return (
    <>
      <header className="header">
        <nav className="navbar">
          <div className="logo">
            Mc<span>Burger</span>King
          </div>
          <FontAwesomeIcon
            icon={faBars}
            className="faBars"
            onClick={() => setShowMenu(!showMenu)}
          />
          <ul className="nav-items" id={showMenu ? "hidden" : ""}>
            <Link
              to="Home"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              Home
            </Link>
            <Link
              to="Menu"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              Menu
            </Link>
            <Link
              to="AboutUs"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              About Us
            </Link>
            <Link
              to="Location"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              Location
            </Link>
            <Link
              to="OpeningHours"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-80}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              Opening Hours
            </Link>

            <Link
              to="ContactUs"
              style={{ padding: "5px 10px" }}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              onClick={() => setShowMenu(!showMenu)}
            >
              Contact Us
            </Link>
          </ul>
        </nav>
      </header>
    </>
  );
}

export default Navbar;
