import React from "react";
import "./OpeningHours.css";

function OpeningHours() {
  return (
    <>
      <div className="hours-container" id="OpeningHours">
        <div className="hours-container-flex">
          <h3>Opening Hours</h3>
          <div className="hours-items">
            <p>Monday: Closed</p>
            <p>Tuesday: Closed</p>
            <p>Wednesday: Closed</p>
            <p>Thursday: Closed</p>
            <p>Friday: Closed</p>
            <p>Saturday: Closed</p>
            <p>Sunday: Closed</p>
            <br />
            <p>Thanks Covid!</p>
          </div>
        </div>
      </div>
    </>
  );
}

export default OpeningHours;
