import React from "react";

function Location() {
  return (
    <div className="map-section">
      <div className="map" id="Location">
        <iframe
          title="location"
          width="100%"
          height="550"
          frameBorder="0"
          scrolling="no"
          marginHeight="0"
          marginWidth="0"
          src="https://maps.google.com/maps?width=100%25&amp;height=600&amp;hl=en&amp;q=boston+(My%20Business%20Name)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"
        />
      </div>
    </div>
  );
}

export default Location;
